import React, { FC, ReactElement, useState } from "react";
import { MdCheckBox, MdCheckBoxOutlineBlank } from "react-icons/md";
import {
  FC_SavePostDetails,
  hierarchyDataInterface,
  RegisterPositionAssets,
  RegisterPositionAssets__Position,
  RegisterPositionAssets__Specialization,
  RequestedVacantDetailsInterface,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import Modal, { Themes, ModalSize } from "../../components/Modal/Modal";
import SelectCustom from "../../components/SelectCustom/SelectCustom";
import { search } from "../../utils/functions";

type AddNewRequestProps = {
  close: () => void;
  assets: RegisterPositionAssets | null;
  loading_assets: boolean;
  saveData: (data: RequestedVacantDetailsInterface) => void;
  school_name: string;
  school_code: string;
  user_id: string;
  hierarchy_data: hierarchyDataInterface[];
};

interface ErrorDisplay {
  target:
    | "position"
    | "specialization"
    | null
    | "main-error"
    | "main-success"
    | "subjects";
  error: string;
}
let defaultErrorMessage: ErrorDisplay = {
  target: null,
  error: "",
};

const AddNewRequest: FC<AddNewRequestProps> = ({
  close,
  loading_assets,
  assets,
  saveData,
  school_code,
  user_id,
  hierarchy_data,
}): ReactElement => {
  const [subjects, setSubjects] = useState<hierarchyDataInterface[]>([]);
  const [search_subject, setSearchSubject] = useState<string>("");
  const [position_open, setPosition_open] = useState(false);
  const [specialization_open, setSpecialization_open] = useState(false);
  const [error, setError] = useState<ErrorDisplay>(defaultErrorMessage);
  const [loading, setLoading] = useState<boolean>(false);

  const initialValue = 0;
  const sumWithInitial_teachingPeriods = subjects.reduce(
    (previousValue, currentValue) =>
      previousValue + +currentValue.teaching_period,
    initialValue
  );

  let ResultsSubjects: hierarchyDataInterface[] = search(
    hierarchy_data,
    search_subject,
    {
      level_code: true,
      level_name: true,
      combination_code: true,
    }
  );

  //* Selected Data
  const [selected_position, setSelectedPosition] =
    useState<RegisterPositionAssets__Position | null>(null);
  const [selected_specialization, setSelectedSpecialization] =
    useState<RegisterPositionAssets__Specialization | null>(null);

  const submitData = (e: any) => {
    e.preventDefault();

    if (selected_position === null)
      return setError({ target: "position", error: "Required" });

    if (selected_specialization === null)
      return setError({ target: "specialization", error: "Required" });

    if (subjects.length === 0) {
      setError({
        target: "subjects",
        error: "Please select subject(s)",
      });

      setTimeout(() => {
        setError({
          target: null,
          error: "",
        });
      }, 2000);
      return;
    }

    setError(defaultErrorMessage);
    setLoading(true);

    FC_SavePostDetails(
      {
        district_code: school_code.substring(0, 2),
        school_code: school_code,
        occupation: selected_position.position_code,
        specialization_id: selected_specialization.specialization_id,
        subjects: subjects.map((el) => el.school_study_hierarchy_id),
        education_level: "1",
        need_post: "",
        requested_by: user_id,
        approved_by: "",
        approved_date: "",
        status: "WAITING",
        total_period: sumWithInitial_teachingPeriods,
      },
      (status, data, msg) => {
        if (status === true) {
          saveData({
            school_need_post_id: data?.id + "",
            district_code: school_code,
            district_name: "",
            school_code: school_code,
            school_name: "",
            occupation: selected_position.position_code,
            position_name: selected_position.position_name,
            specialization_id: selected_specialization.specialization_id,
            specialization_name: selected_specialization.specialization_name,
            education_level: "",
            qualification_name: selected_position.qualification_name,
            need_post: "",
            status: "WAITING",
            total_period: sumWithInitial_teachingPeriods,
          });

          setLoading(false);
          setError({
            target: "main-success",
            error: "New request created successfully",
          });
          setTimeout(() => {
            close();
          }, 1000);
        } else {
          setLoading(false);
          setError({
            target: "main-error",
            error: `Error: ${msg}`,
          });
        }
      }
    );
  };

  const closeHandler = () => {
    if (
      (selected_position !== null || selected_specialization !== null) &&
      window.confirm(
        "Once you close you will lose the selected data, \n do ypu want to close?"
      )
    ) {
      close();
    } else {
      close();
    }
  };

  const selectSubject = (subj: hierarchyDataInterface) => {
    console.log(subj, subjects);

    let exist = subjects.find(
      (el) => el.school_study_hierarchy_id === subj.school_study_hierarchy_id
    );

    if (!exist) {
      setSubjects([...subjects, subj]);
    } else {
      setSubjects(
        subjects.filter(
          (el) =>
            el.school_study_hierarchy_id !== subj.school_study_hierarchy_id
        )
      );
    }
  };

  return (
    <>
      <Modal
        backDrop={true}
        theme={Themes.default}
        close={closeHandler}
        backDropClose={false}
        widthSizeClass={ModalSize.large}
        displayClose={true}
        title="Add new post on school"
        padding={{
          title: true,
          body: undefined,
          footer: undefined,
        }}
      >
        {error.target === "main-error" && (
          <Alert theme="danger">{error.error}</Alert>
        )}
        {error.target === "main-success" && (
          <Alert theme="success">{error.error}</Alert>
        )}

        <form onSubmit={submitData}>
          {loading_assets === true ? (
            <div className="py-8 text-center mx-auto">
              <Loading />
            </div>
          ) : assets === null ? (
            <div className={`py-8 text-center text-lg text-red-600`}>
              No data found!
            </div>
          ) : (
            <div className="border-t p-4">
              <div className="pb-3">
                {position_open ? (
                  <SelectCustom
                    loading={false}
                    id={"position_code"}
                    title={"title"}
                    close={() => setPosition_open(!position_open)}
                    data={assets.school_positions.map((el) => ({
                      ...el,
                      title: `${el.position_code} - ${el.position_name} (${el.qualification_name})`,
                    }))}
                    click={(pos: RegisterPositionAssets__Position) => {
                      setSelectedPosition(pos);
                      setPosition_open(false);
                    }}
                  />
                ) : (
                  <div
                    onClick={() =>
                      loading === false && setPosition_open(!position_open)
                    }
                  >
                    <TextInput
                      onChange={(e) => {}}
                      value={
                        selected_position
                          ? `${selected_position.position_code} - ${selected_position.position_name} (${selected_position.qualification_name})`
                          : ""
                      }
                      error={error.target === "position" ? error.error : ""}
                      label={"Position"}
                      placeholder={"Position"}
                      name={""}
                      disabled
                    />
                  </div>
                )}
              </div>

              <div
                className="pb-3"
                onClick={() =>
                  loading === false && setPosition_open(!position_open)
                }
              >
                <TextInput
                  onChange={(e) => {}}
                  value={
                    selected_position
                      ? `${selected_position.qualification_name}`
                      : ""
                  }
                  error={error.target === "position" ? error.error : ""}
                  label={"Level of Education"}
                  placeholder={"Select the position"}
                  name={""}
                  disabled
                />
              </div>

              <div className="pb-3">
                {specialization_open ? (
                  <SelectCustom
                    loading={false}
                    id={"id"}
                    title={"title"}
                    close={() => setSpecialization_open(!specialization_open)}
                    data={assets.specializations.map((el) => ({
                      ...el,
                      title: `${el.specialization_name}`,
                      id: el.specialization_id,
                    }))}
                    click={(el: RegisterPositionAssets__Specialization) => {
                      setSpecialization_open(false);
                      setSelectedSpecialization(el);
                    }}
                  />
                ) : (
                  <div
                    onClick={() =>
                      loading === false &&
                      setSpecialization_open(!specialization_open)
                    }
                  >
                    <TextInput
                      onChange={(e) => {}}
                      value={
                        selected_specialization
                          ? selected_specialization.specialization_name
                          : ""
                      }
                      error={
                        error.target === "specialization" ? error.error : ""
                      }
                      label={"Specialization"}
                      placeholder={"Specialization"}
                      name={"specialization_name"}
                      disabled
                    />
                  </div>
                )}
              </div>

              <div
                className={`pb-2 bg-gray-100 mb-3 mt-1 rounded  ${
                  error.target === "subjects" ? "border-red-500 border-2" : ""
                }`}
              >
                <div className="pb-1 flex items-center px-2">
                  <section className="flex-1">
                    <TextInput
                      onChange={(e) => setSearchSubject(e.target.value)}
                      value={search_subject}
                      error={""}
                      label={""}
                      placeholder={"Search subject"}
                      name={""}
                      size="sm"
                    />
                  </section>
                  <section className="px-2 rounded bg-gray-100">
                    <span className="text-sm text-gray-400">Subjects:</span>
                    <h4 className="text-xl -mt-1 font-bold">
                      {subjects.length}
                    </h4>
                  </section>
                  <section className="px-2 rounded bg-gray-100">
                    <span className="text-sm text-gray-400">
                      Total periods:
                    </span>
                    <h4 className="text-xl -mt-1 font-bold">
                      {sumWithInitial_teachingPeriods}
                    </h4>
                  </section>
                </div>
                <div className="h-56 overflow-y-auto">
                  <table className="w-full">
                    <thead>
                      <tr>
                        <th className="border p-1"></th>
                        <th className="border p-1">Course name</th>
                        <th className="border p-1">Combination</th>
                        <th className="border p-1">Grade name</th>
                        <th className="border p-1">Teaching period</th>
                      </tr>
                    </thead>
                    <tbody className="">
                      {ResultsSubjects.map((el, i) => {
                        let exist = subjects.find(
                          (itm) =>
                            el.school_study_hierarchy_id ===
                            itm.school_study_hierarchy_id
                        );
                        return (
                          <tr
                            key={i}
                            className={`cursor-pointer ${
                              exist === undefined
                                ? ""
                                : "bg-green-200 bg-opacity-40"
                            }`}
                            onClick={selectSubject.bind(this, el)}
                          >
                            <td
                              className={`border p-1 text-center text-2xl ${
                                exist === undefined ? "" : "text-green-500"
                              }`}
                            >
                              {exist === undefined ? (
                                <MdCheckBoxOutlineBlank />
                              ) : (
                                <MdCheckBox />
                              )}
                            </td>
                            <td className="border p-1">{el.course_name}</td>
                            <td className="border p-1">
                              {el.combination_name}
                            </td>
                            <td className="border p-1">{el.grade_name}</td>
                            <td className="border p-1">{el.teaching_period}</td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </div>
              </div>
              {error.target === "subjects" && (
                <span className="text-red-800 text-center">{error.error}</span>
              )}

              <div className="pb-2 text-right">
                {error.target !== "main-success" && (
                  <Button type="submit" loading={loading}>
                    Request New vacant
                  </Button>
                )}
              </div>
            </div>
          )}
        </form>
      </Modal>
    </>
  );
};
export default AddNewRequest;
