import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  FC_DeleteRequestedPostDescription,
  FC_GetPositionsAssets,
  FC_GetVacantDetailedBySchool,
  hierarchyDataInterface,
  RegisterPositionAssets,
  RequestedVacantDetailsInterface,
  FC_GetCoursesBySchools,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import AddNewRequest from "./AddNewRequest";

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading_assets: boolean;
  loading_data: boolean;
  create_request: boolean;
  assets: RegisterPositionAssets | null;
  error: {
    target: "main-error" | "main-success" | null;
    msg: string;
  };
  data: RequestedVacantDetailsInterface[];
  hierarchy_data: hierarchyDataInterface[];
  loading_hierarchy: boolean;
  delete_loading: boolean;
  deleting_item: string;
}

class _RequestVacantPositions extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      error: {
        target: null,
        msg: "",
      },
      loading_assets: false,
      create_request: false,
      assets: null,
      data: [],
      loading_data: false,
      hierarchy_data: [
        // {
        //   school_study_hierarchy_id: "32465",
        //   school_code: "520104",
        //   shool_hierarch_id: "22",
        //   status: "1",
        //   studyhierarchyid: "22",
        //   level_code: "1",
        //   level_name: "PRIMARY",
        //   combination_code: "101",
        //   combination_name: "PRIMARY",
        //   grade_code: "1201",
        //   grade_name: "PRIMARY 1",
        //   course_code: "01",
        //   course_name: "Kinyarwanda 3",
        //   teaching_period: "8",
        // },
        // {
        //   school_study_hierarchy_id: "32466",
        //   school_code: "520104",
        //   shool_hierarch_id: "22",
        //   status: "1",
        //   studyhierarchyid: "22",
        //   level_code: "1",
        //   level_name: "PRIMARY",
        //   combination_code: "101",
        //   combination_name: "PRIMARY",
        //   grade_code: "1201",
        //   grade_name: "PRIMARY 1",
        //   course_code: "01",
        //   course_name: "French",
        //   teaching_period: "8",
        // },
        // {
        //   school_study_hierarchy_id: "32467",
        //   school_code: "520104",
        //   shool_hierarch_id: "22",
        //   status: "1",
        //   studyhierarchyid: "22",
        //   level_code: "1",
        //   level_name: "PRIMARY",
        //   combination_code: "101",
        //   combination_name: "PRIMARY",
        //   grade_code: "1201",
        //   grade_name: "PRIMARY 1",
        //   course_code: "01",
        //   course_name: "History",
        //   teaching_period: "8",
        // },
        // {
        //   school_study_hierarchy_id: "32448",
        //   school_code: "520104",
        //   shool_hierarch_id: "22",
        //   status: "1",
        //   studyhierarchyid: "22",
        //   level_code: "1",
        //   level_name: "PRIMARY",
        //   combination_code: "101",
        //   combination_name: "PRIMARY",
        //   grade_code: "1201",
        //   grade_name: "PRIMARY 1",
        //   course_code: "01",
        //   course_name: "KoStant",
        //   teaching_period: "8",
        // },
      ],
      loading_hierarchy: false,
      delete_loading: false,
      deleting_item: "",
    };
  }

  componentDidMount(): void {
    if (this.props.auth.school === null)
      return this.setState({
        error: {
          target: "main-error",
          msg: "No school assigned to you",
        },
      });

    this.setState({
      loading_assets: true,
      loading_data: true,
      loading_hierarchy: true,
    });

    FC_GetPositionsAssets(
      this.props.auth.school.school_code,
      (status, data, err) => {
        if (status === true) {
          this.setState({
            loading_assets: false,
            assets: data,
          });
        } else {
          this.setState({
            loading_assets: false,
            error: {
              target: "main-error",
              msg: `Error: ${err}`,
            },
          });
        }
      }
    );

    // get the request
    FC_GetVacantDetailedBySchool(
      this.props.auth.school.school_code,
      (status, data_vac, msg) => {
        if (status === true) {
          this.setState({
            data: data_vac,
            loading_data: false,
          });
        } else {
          this.setState({
            data: [],
            loading_data: false,
            error: {
              target: "main-error",
              msg: `Error: ${msg}`,
            },
          });
        }
      }
    );

    // // Get all the data
    FC_GetCoursesBySchools(
      this.props.auth.school.school_code,
      (status, data_hierarch, msg) => {
        if (status === true) {
          this.setState({
            hierarchy_data: data_hierarch,
            loading_hierarchy: false,
          });
        } else {
          this.setState({
            hierarchy_data: [],
            loading_hierarchy: false,
            error: {
              target: "main-error",
              msg: `Error: ${msg}`,
            },
          });
        }
      }
    );
  }

  cleanError = () =>
    this.setState({
      error: {
        target: null,
        msg: "",
      },
    });

  addNewItem = (data: RequestedVacantDetailsInterface) =>
    this.setState({
      data: [...this.state.data, data],
    });

  removeItem = (id: string) => {
    if (window.confirm("Are you sure you want to remove this record?")) {
      this.setState({
        delete_loading: true,
        deleting_item: "",
      });

      FC_DeleteRequestedPostDescription(id, (status, msg) => {
        if (status === true) {
          this.setState({
            delete_loading: false,
            deleting_item: "",
            error: {
              target: "main-success",
              msg: "Record removed successfully",
            },
            data: this.state.data.filter((el) => el.school_need_post_id !== id),
          });
        } else {
          this.setState({
            delete_loading: false,
            deleting_item: "",
            error: {
              target: "main-error",
              msg: `Error: ${msg}`,
            },
          });

          setTimeout(() => {
            this.setState({
              error: {
                msg: "",
                target: null,
              },
            });
          }, 2000);
        }
      });
    }
  };

  render() {
    let totalA0 = this.state.data.filter(
      (el) => el.qualification_name === "A0"
    ).length;
    let totalA1 = this.state.data.filter(
      (el) => el.qualification_name === "A1"
    ).length;
    let totalA2 = this.state.data.filter(
      (el) => el.qualification_name === "A2"
    ).length;
    return (
      <>
        {this.state.error.target === "main-error" && (
          <Alert theme="danger" fixed onClick={this.cleanError}>
            {this.state.error.msg}
          </Alert>
        )}
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed onClick={this.cleanError}>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.create_request === true &&
          this.props.auth.school !== null &&
          this.props.auth.user && (
            <AddNewRequest
              close={() =>
                this.setState({
                  create_request: false,
                })
              }
              loading_assets={this.state.loading_assets}
              assets={this.state.assets}
              saveData={this.addNewItem}
              school_code={this.props.auth.school.school_code}
              school_name={this.props.auth.school.school_name}
              user_id={this.props.auth.user.user_id}
              hierarchy_data={this.state.hierarchy_data}
            />
          )}

        <DashboardContainer>
          <div className="w-full flex items-center">
            <section className="flex-1">
              <Heading size="md">Describe needed posts</Heading>
              <p className="m-0 text-gray-400 text-sm">
                Specify all the position without the vacant
              </p>
            </section>
            <section>
              <Button
                type="button"
                onClick={() =>
                  this.setState({ create_request: !this.state.create_request })
                }
              >
                Add new vacant
              </Button>
            </section>
          </div>
        </DashboardContainer>

        <DashboardContainer>
          <div className="flex gap-2 mb-2">
            <section className="border p-1 px-6 rounded bg-gray-100">
              <span>A0</span>
              <h2 className="font-bold text-2xl -mt-1">{totalA0}</h2>
            </section>
            <section className="border p-1 px-6 rounded bg-gray-100">
              <span>A1</span>
              <h2 className="font-bold text-2xl -mt-1">{totalA1}</h2>
            </section>
            <section className="border p-1 px-6 rounded bg-gray-100">
              <span>A2</span>
              <h2 className="font-bold text-2xl -mt-1">{totalA2}</h2>
            </section>
            <div className="flex-1"></div>
            <section className="border p-1 px-6 rounded bg-gray-100">
              <span>Total</span>
              <h2 className="font-bold text-2xl -mt-1">
                {totalA2 + totalA1 + totalA0}
              </h2>
            </section>
          </div>
          <table className="w-full">
            <thead>
              <tr className="text-left">
                <th className="p-2 border">#</th>
                <th className="p-2 border">Code</th>
                <th className="p-2 border">Position name</th>
                <th className="p-2 border">Qualification </th>
                <th className="p-2 border">Specialization</th>
                <th className="p-2 border">Total Period</th>
                <th className="p-2 border">Status</th>
                <th className="p-2 border"></th>
              </tr>
            </thead>
            <tbody>
              {this.state.loading_data === true ? (
                <tr>
                  <td className="py-8 text-center" colSpan={10}>
                    <Loading />
                  </td>
                </tr>
              ) : this.state.data.length === 0 ? (
                <tr>
                  <td className="py-8 text-center" colSpan={10}>
                    <h2 className="text-2xl font-bold text-gray-600">
                      No results found
                    </h2>
                  </td>
                </tr>
              ) : (
                <>
                  {this.state.data.map((el, i) => (
                    <tr key={i}>
                      <td className="border p-2">{i + 1}</td>
                      <td className="border p-2">{el.school_code}</td>
                      <td className="border p-2">{el.position_name}</td>
                      <td className="border p-2">{el.qualification_name}</td>
                      <td className="border p-2">{el.specialization_name}</td>
                      <td className="border p-2">{el.total_period}</td>
                      <td className="border p-2 text-center">
                        <span
                          className={`p-1 rounded-lg text-xs ${
                            el.status === "APPROVED"
                              ? "bg-green-100 text-green-700"
                              : "bg-gray-200"
                          }`}
                        >
                          {el.status}
                        </span>
                      </td>
                      <td className="border p-2 text-center">
                        <Button
                          theme="danger-light"
                          type="button"
                          onClick={this.removeItem.bind(
                            this,
                            el.school_need_post_id
                          )}
                          disabled={this.state.delete_loading}
                          loading={
                            this.state.deleting_item === el.school_need_post_id
                              ? true
                              : false
                          }
                        >
                          Remove
                        </Button>
                      </td>
                    </tr>
                  ))}
                </>
              )}
            </tbody>
          </table>
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const RequestVacantPositions = connect(
  mapStateToProps,
  {}
)(_RequestVacantPositions);
